footer {
  height: 450px;
  background-color: $dark-color;
  color: #fff !important;
  line-height: 1.6 !important;

  .footer-content {
    padding-left: 200px;
    padding-right: 200px;
    padding-top: 100px;
  }

  img {
    height: 75px;
  }

  h2 {
    color: white;
    text-align: center;
    line-height: .1;
  }

  h4 {
    color: white;
    text-align: center;
    line-height: 1.5;
    margin-top: 12px;
  }

  .fancy {
    font-family: 'Source Serif Pro', serif;
    line-height: 10% !important;
  }

  ul {

    padding:0;
    text-align: center;
    display: block;
    margin-top: 15px;
  }

   li {
     display:inline;
     word-spacing: 15px;


     a {
       color: #fff;
       text-decoration: none !important;
       transition: .5s;
     }
     a:hover{
       color: $secondary-color;
     }
   }

   .border-between > [class*='col-']:before {
   background: #e3e3e3;
   bottom: 0;
   content: " ";
   left: 0;
   position: absolute;
   width: 1px;
       top: 0;
    }

    .border-between > [class*='col-']:first-child:before {
       display: none;
    }
}
